@media (min-width: 768px) {
  .footer__contacts {
    padding-left: 13px;
  }
}

.footer__contacts-address {
  position: relative;
  margin-top: 3px;
  margin-bottom: 17px;
  padding-left: 19px;
}
.footer__contacts-address::before {
  position: absolute;
  content: "";
  left: 0;
  top: 4px;
  height: 16px;
  width: 13px;
  background: url("../img/address.svg") no-repeat center;
}

.footer__contacts-phone {
  position: relative;
  padding-left: 19px;
  text-decoration: none;
  color: #333333;
}
.footer__contacts-phone::before {
  position: absolute;
  content: "";
  left: 0;
  top: 4px;
  height: 16px;
  width: 13px;
  background: url("../img/phone.svg") no-repeat center;
}

.footer__copyright {
  position: relative;
  grid-area: copyright;
  padding: 0 20px;
  padding-top: 8px;
  padding-bottom: 20px;
}
.footer__copyright::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .footer__copyright::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .footer__copyright {
    padding-top: 0;
  }
}

.footer__copyright-text {
  display: flex;
  padding-top: 14px;
  padding-left: 37px;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: #bdbdbd;
}
@media (min-width: 768px) {
  .footer__copyright-text {
    justify-content: center;
    padding-top: 0;
    padding-left: 0;
  }
}

.footer__copyright-icon {
  padding-left: 4px;
}

.footer__copyright-link {
  display: flex;
  align-items: center;
  padding-left: 3px;
  text-decoration: none;
  color: #bdbdbd;
}
.footer__copyright-link:hover, .footer__copyright-link:focus {
  color: #7859cf;
  outline: none;
}
.footer__copyright-link:hover .footer__copyright-icon path, .footer__copyright-link:focus .footer__copyright-icon path {
  fill: #7859cf;
}
.footer__copyright-link:active {
  color: #593cac;
}
.footer__copyright-link:active .footer__copyright-icon path {
  fill: #593cac;
}

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 160px;
  padding-right: 10px;
  padding-top: 8px;
  padding-bottom: 10px;
}
@media (min-width: 1440px) {
  .social-list {
    justify-self: flex-start;
    margin-left: 30px;
    padding-top: 0;
  }
}

.social-list__item {
  width: 40px;
  height: 40px;
}

.social-list__link {
  width: 40px;
  height: 40px;
  border-radius: 4px;
}
.social-list__link > svg path {
  fill: #bdbdbd;
}
.social-list__link:hover, .social-list__link:focus {
  outline: none;
}
.social-list__link:hover svg path, .social-list__link:focus svg path {
  fill: #9070ec;
}
.social-list__link:active svg path {
  fill: #7859cf;
}